.about {
  padding-top: 61px;
  padding-bottom: 50px;
  background: #84a178;

  @media screen and (min-width: 768px) {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  @media screen and (min-width: 1200px) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.about__text {
  margin-bottom: 12px;
  text-align: center;

  font-weight: 700;
  font-size: 18px;
  line-height: 1.22;
  text-align: center;
  text-transform: uppercase;

  color: #fcfcfc;

  @media screen and (min-width: 768px) {
    font-weight: 800;
    font-size: 22px;
    line-height: 1.23;
    letter-spacing: 0.04em;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
  }
}

.about__title {
  width: 200px;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 32px;

  font-family: 'Seymour One', sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 1.27;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #fcfcfc;

  @media screen and (min-width: 768px) {
    width: 100%;
    margin-bottom: 72px;

    font-size: 34px;
    line-height: 1.26;
    letter-spacing: 0.06em;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 82px;

    font-size: 36px;
    line-height: 1.25;
  }
}

.about__thumb {
  position: relative;

  width: 278px;
  height: 278px;
  margin: 0 auto;
  border-radius: 50%;
  background: linear-gradient(
      0deg,
      rgba(248, 210, 78, 0.7),
      rgba(248, 210, 78, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 4.48583px 16.8218px rgba(153, 114, 23, 0.25);
  border-radius: 140px;

  @media screen and (min-width: 768px) {
    width: 330px;
    height: 330px;
    border-radius: 50%;
  }

  @media screen and (min-width: 1200px) {
    margin: 0;
    width: 490px;
    height: 490px;
    border-radius: 50%;
  }
}

.about__thumb::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  top: 22px;
  transform: translate(-50%);

  background-image: url(./../images/about/about_s1x.png);
  background-size: cover;

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(./../images/about/about_s2x.png);
  }

  @media screen and (min-width: 481px) {
    background-image: url(./../images/about/about_m1x.png);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(./../images/about/about_m2x.png);
    }
  }

  @media screen and (min-width: 769px) {
    top: 26px;
    background-image: url(./../images/about/about_l1x.png);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(./../images/about/about_l2x.png);
    }
  }

  @media screen and (min-width: 1200px) {
    top: 39px;
  }
}

.about__wrap {
  display: block;
  text-align: center;

  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
    gap: 38px;
  }

  @media screen and (min-width: 1200px) {
    display: flex;
    justify-content: center;
    gap: 109px;
  }
}

.about__desc {
  padding-top: 40px;
  margin: 0 auto;
  width: 220px;

  @media screen and (min-width: 768px) {
    width: 336px;
  }

  @media screen and (min-width: 1200px) {
    width: 463px;
  }
}

.about__img-bottom {
  position: relative;
  width: 278px;
  height: 278px;
  border-radius: 50%;
  background: linear-gradient(
      0deg,
      rgba(248, 210, 78, 0.7),
      rgba(248, 210, 78, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 8px 30px rgba(153, 114, 23, 0.25);
  border-radius: 250px;
  margin: 0 auto;
}

.about__img-bottom::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(./../images/about/about_s1x.png);
  top: 22px;
}

.about__desc-one {
  margin-bottom: 4px;
 
  font-weight: 400;
  font-size: 12px;
  line-height: 1.76;

  text-align: center;
  letter-spacing: 0.04em;

  color: #fcfcfc;

  @media screen and (min-width: 768px) {
    margin-bottom: 8px;
    text-align: left;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
  }
}

.about__desc-two {
  margin-bottom: 4px;

  font-weight: 400;
  font-size: 12px;
  line-height: 1.62;

  text-align: center;

  color: #fcfcfc;

  @media screen and (min-width: 768px) {
    margin-bottom: 8px;
    line-height: 1.76;
    letter-spacing: 0.04em;
    text-align: left;
  }

  @media screen and (min-width: 1200px) {
    font-size: 14px;
    line-height: 1.94;
  }
}

.about__desc-three {
  margin-bottom: 10px;
  font-size: 12px;
  line-height: 1.94;
  letter-spacing: 0.04em;

  text-align: center;

  color: #fcfcfc;

  @media screen and (min-width: 768px) {
    margin-bottom: 32px;
    font-size: 12px;
    text-align: left;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 54px;
    font-size: 14px;
  }
}

.about__btn {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  gap: 8px;

  width: 164px;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 34px;
  padding-right: 16px;
  margin-left: auto;
  margin-right: auto;

  font-weight: 600;
  font-size: 14px;
  line-height: 1.29;

  border-radius: 10px;
  border: none;
  cursor: url("data:image/svg+xml;charset=utf8,%3Csvg width='19' height='24' viewBox='0 0 19 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.04696 2.01171L16.9811 12.2169C18.8522 13.9633 17.8177 17.1 15.2748 17.3907L11.1573 17.8612C10.124 17.9793 9.15329 18.4167 8.38025 19.1124L6.0069 21.2484C4.07633 22.9859 1 21.6159 1 19.0186V4.20487C1 1.58174 4.12929 0.221893 6.04696 2.01171Z' stroke='%23131313' stroke-width='2'/%3E%3C/svg%3E"),
    pointer;

  color: #000000;
  background-color: #f8d24e;

  transition: background-color 0.25s cubic-bezier(0.4, 0, 0.2, 1),
    color 0.25s cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    margin-left: 0;
  }

  @media screen and (min-width: 1200px) {
    margin-left: 0;
  }

  &:hover {
    background-color: #0a8817;
    color: #000000;
  }

  &:focus {
    background-color: #0a8817;
    color: #fcfcfc;
  }

  &:disabled {
    background-color: #ababab;
    color: #000000;
  }
}

.about__icon {
  fill: #000000;
}

.about__btn:focus .about__icon {
  fill: #fcfcfc;
}
