html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Montserrat', sans-serif;
  //      -
  // font-family: 'Seymour One', sans-serif;

  // background-image
  background-repeat: repeat;
  background-attachment: fixed;
  //mobile
  @include media('phone', max) {
    background-image: url(./../images/backgroud_image/backgroud_m@1x.png);
    background-size: 320px 449px;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(./../images/backgroud_image/backgroud_m@2x.png);
    }
  }
  //tablet
  @include min-max('phone', 'desktop') {
    background-image: url(./../images/backgroud_image/background_t@1x.png);
    background-size: 720px 728px;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(./../images/backgroud_image/background_t@2x.png);
    }
  }
  //Desktop
  @include media('desktop') {
    background-image: url(./../images/backgroud_image/backgroud_d@1x.png);
    background-size: 1200px 1248px;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(./../images/backgroud_image/backgroud_d@2x.png);
    }
  }

  &::-webkit-scrollbar {
    width: 20px;
    background: linear-gradient(to top, #f2994a, #f2c94c);
  }
  &::-webkit-scrollbar-track {
    border: 2px solid getColor('link-hover');
    box-shadow: inset 2.5px 2.5px 5px getColor('main-bg');
  }
  &::-webkit-scrollbar-thumb {
    background: linear-gradient(to top, #dce35b, #45b649);
    border: 2px solid #ee508f;
    border-radius: 15px;
  }
}

// 
.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;
  max-width: 480px;

  @include media('tablet') {
    padding-right: 32px;
    padding-left: 32px;
    max-width: 768px;
  }

  @include media('desktop') {
    padding-right: 16px;
    padding-left: 16px;
    max-width: 1200px;
  }
}

//     
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

// .body.is-open {
