    margin-right: 60px;
    width: 106px;
    margin-bottom: 0;
  }
}
.header__form-chebox input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.header__checkbox + label {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: calc(20 / 16);
  text-align: center;
  letter-spacing: 0.04em;
  color: #131313;
  padding-top: 20px;
  padding-bottom: 20px;
}
.header__checkbox.ice__cream + label::after {
  top: -75px;
  left: 42px;
  @media screen and (min-width: 768px) {
    top: -73px;
    left: -17px;
  }
  content: '';
  width: 132px;
  height: 136px;
  border: 2px solid #96af8c;
  filter: drop-shadow(0px 5.26027px 19.726px rgba(212, 20, 67, 0.1));
  border-radius: 15px;
  position: absolute;
  display: inline-block;
}
.header__checkbox.ice__cream:checked + label::after {
  background: linear-gradient(
      0deg,
      rgba(156, 190, 143, 0.7),
      rgba(156, 190, 143, 0.7)
    ),
    #d9d9d9;
  z-index: -1;
}
.header__checkbox.ice__cream + label::before {
  content: '';
  background: linear-gradient(
      0deg,
      rgba(156, 190, 143, 0.7),
      rgba(156, 190, 143, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 5.26027px 19.726px rgba(0, 0, 0, 0.25);
  position: absolute;
  top: -132px;
  left: 107px;
