.backdrop {
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);

  // overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal-franchise {
  position: absolute;
  top: 2%;
  left: 2%;
  width: 95vw;

  padding: 30px;

  background: #fcfcfc;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border: 2px solid #f8d24e;
  border-radius: 10px;

  transition: opacity 800ms 500ms ease;
  overflow-y: auto;

  @media screen and (min-width: 320px) {
    width: 280px;
    height: 95vh;
    max-height: 674px;

    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
  }

  @include media('tablet') {
    top: 50%;

    width: 568px;
    height: 95vh;
    max-height: 668px;

    padding-bottom: 32px;
    padding-left: 58px;
    padding-right: 58px;
  }

  .franchise__form {
    @include media('tablet') {
      padding-left: 40px;
      padding-right: 40px;
    }
  }

  .franchise__wrap {
    position: relative;
    margin-bottom: 12px;

    @include media('tablet') {
      margin-bottom: 16px;
    }
  }

  &__btn {
    position: absolute;
    top: 12px;
    right: 12px;
    padding: 0;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    background-color: #ffffff;
    border: none;
    outline: 0;
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

    @include media('tablet') {
      width: 24px;
      height: 24px;
    }
  }

  // @include media('desktop') {
  //   overflow-y: hidden;
  // }

  &::-webkit-scrollbar {
    width: 10px;
    background: linear-gradient(to top, #c9d6ff, #e2e2e2);
  }
  &::-webkit-scrollbar-track {
    border: 1px solid getColor('link-default');
  }
  &::-webkit-scrollbar-thumb {
    background: linear-gradient(to top, #ffefba, #ffffff);
    border: 1px solid #ee508f;
    border-radius: 15px;
  }
}

.franchise__title {
  display: block;
  margin-bottom: 20px;

  max-width: 322px;
  margin-left: auto;
  margin-right: auto;

  font-family: 'Seymour One', sans-serif;

  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  text-align: center;
  color: #131313;

  @include media('tablet') {
    font-size: 20px;
    line-height: 1.4;
  }
}

.franchise__text {
  display: block;
  margin-bottom: 24px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  color: #131313;

  @include media('tablet') {
    margin-bottom: 32px;
    font-size: 16px;
    line-height: 1.38;
  }
}

.franchise__textarea {
  position: relative;
  margin-bottom: 24px;
  height: 64px;

  @include media('tablet') {
    margin-bottom: 32px;
    height: 84px;
  }
}

.franchise__textarea .comments {
  top: 17px;
  left: 12px;

  @include media('tablet') {
    top: 20px;
    left: 16px;
  }
}

.franchise__field,
.franchise__comment {
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 8px;
  padding-bottom: 7px;
  width: 100%;
  background: rgba(248, 210, 78, 0.1);
  border: 1px solid #84a178;
  border-radius: 10px;
  outline: none;
  transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &::placeholder {
    color: getColor('secondary-text');
  }

  @include media('tablet') {
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 11px;
    padding-bottom: 11px;
  }
}

.franchise__comment {
  resize: none;

  &::placeholder {
    color: getColor('secondary-text');
  }
}

.franchise__field:focus,
.franchise__comment:focus {
  border: 1px solid #0a8817;
}

.franchise__btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 42px;

  padding: 12px 34px;

  text-align: center;

  border: none;
  border-radius: 10px;
  color: #131313;
  background-color: #f8d24e;

  font-family: 'Montserrat';
  font-weight: 600;
  font-size: 14px;
  line-height: 1.21;

  min-width: 114px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0;
  margin-top: 12px;
  background-color: #f8d24e;
  border-radius: 10px;

  @include media('tablet') {
    min-width: 121px;
  }
}

.modal-franchise__icon {
  fill: currentColor;
  width: 20px;
  height: 20px;
  @include media('tablet') {
    width: 24px;
    height: 24px;
  }
}
